<template>
	<div id="app">
		<!-- 需要缓存 -->
		<keep-alive> 　<router-view v-if="$route.meta.keepAlive"></router-view> </keep-alive>
		<!-- 不需要缓存 -->
		<router-view v-if="!$route.meta.keepAlive"></router-view>
	</div>
</template>

<script>
export default {
	name: 'App',
	data() {
		return {};
	},
	created() {},
	mounted() {},
};
</script>

<style>
* {
	margin: 0;
	padding: 0;
}
html,
body {
	width: 100%;
	height: 100%;
	background-color: #ffff;

}
</style>



