import Vue from "vue";
import Router from "vue-router";
import Home from "@/pages/home";

Vue.use(Router);

let routers = [];

if (router_type == "index") {
  routers = [{ path: "/", name: "Home", component: Home }];
}

const Routers = new Router({
  routes: routers
});

export default Routers;



